	.content-detail {
		display: flex;
		width: 100vw;
		margin-top: 10vh;

		.sticky-image {
			background-image: url(../img/BlackPanter.jpg);
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			width: 30vw;
			height: 90vh;
			position: fixed;

			a {
				text-decoration: none;
			}

			.home-button {
				position: absolute;
				left: 0;
				bottom: 0;
				width: 30vw;
				height: 10vh;
				background: #252525;
				display: flex;
				align-items: center;
				padding-left: 5%;

				& svg {
					font-size: 1.5rem;
					color: $blue;
					margin-right: 5%;
				}


				& p {
					font-family: 'Roboto', sans-serif;
					color: #fff;
				}
			}
		}

		.grid {
			max-width: 69em;
			list-style: none;
			padding: 0;
		}

		.grid li {
			display: block;
			float: left;
			padding: 7px;
			width: 50%;
			opacity: 1;

			// @media screen and (min-width: 600px) {
			// 	width: 33%;
			// }
		}

		.test-container {
			background: red;
			//height: 40vh;

			.test {
				display: flex;
				// flex-direction: column;
				align-items: center;
				justify-content: space-between;

				& img {
					object-fit: contain;
					width: 22vw;
				}
			}
		}

		.grid li.shown,
		.no-js .grid li,
		.no-cssanimations .grid li {
			opacity: 1;
		}

		.grid li a,
		.grid li img {
			outline: none;
			border: none;
			display: block;
			max-width: 100%;
		}

		/* Effect 2: Move Up */
		.grid.effect-2 li.animate {
			-webkit-transform: translateY(200px);
			transform: translateY(200px);
			-webkit-animation: moveUp 0.65s ease forwards;
			animation: moveUp 0.65s ease forwards;
		}

		@-webkit-keyframes moveUp {
			0% { }
			100% { -webkit-transform: translateY(0); opacity: 1; }
		}

		@keyframes moveUp {
			0% { }
			100% { -webkit-transform: translateY(0); transform: translateY(0); opacity: 1; }
		}
	}
}







